import React from 'react';
import logo from '../images/logo.svg';
import menu from '../images/menu.svg';
import close from '../images/close.svg';

class Navigation extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fixed: false,
      overlay: false
    };
  }

  componentDidMount() {
    document.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.handleScroll);
  }

  componentDidUpdate() {
    if (this.state.fixed === true) {
      document.getElementById('navigation').classList
        .add('navigation--fixed');
    } else {
      document.getElementById('navigation').classList
        .remove('navigation--fixed');
    }

    if (this.state.overlay === true) {
      document.getElementById('navigation-list').classList
        .add('navigation__list--overlay');
      document.getElementById('navigation-close').classList
        .add('navigation__close--active');
    } else {
      document.getElementById('navigation-list').classList
        .remove('navigation__list--overlay');
      document.getElementById('navigation-close').classList
        .remove('navigation__close--active');
    }
  }

  handleScroll = () => {
    if (document.documentElement.scrollTop > 0)  {
      this.setState({ fixed: true });
    } else {
      this.setState({ fixed: false });
    }
  }

  handleMenuClick = () => {
    this.setState({ overlay: true });
  }

  handleCloseClick = () => {
    this.setState({ overlay: false });
  }

  render() {
    return(
      <div className="navigation" id="navigation">
        <div className="container">
          <nav aria-label="Main" className="navigation__bar">
            <button className="navigation__logo">
              <img src={logo} alt="Digital Space Agency"
                className="navigation__logo-img"
              />
            </button>
            <div className="navigation__main">
              <button className="navigation__menu"
                onClick={() => this.handleMenuClick()}
              >
                <img src={menu} alt="Menu" className="navigation__menu-img" />
              </button>
              <ul className="list-unstyled navigation__list"
                id="navigation-list"
              >
                <li className="navigation__list-item">
                  <a href="#blog" className="navigation__link">Blog</a>
                </li>
                <li className="navigation__list-item">
                  <a href="#popular" className="navigation__link">Popular</a>
                </li>
                <li className="navigation__list-item">
                  <a href="#archive" className="navigation__link">Archive</a>
                </li>
                <li className="navigation__list-item">
                  <a href="#about" className="navigation__link">About</a>
                </li>
              </ul>
              <button className="navigation__close" id="navigation-close"
                onClick={() => this.handleCloseClick()}
              >
                <img src={close} alt="Close" className="navigation__close-img" />
              </button>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}

export default Navigation;
